const i18n={
	lang:null,
	langs:{},
	_:(str,...args)=>{
		let s=(i18n.lang&&i18n.langs[i18n.lang][str])||str;
		args.length&&args.forEach((arg,ind)=>{s=s.replace(`$${ind}`,arg)});
		return s;
	}
};


i18n.langs['zh-CN']={
	'play':'',
	'loop':'',
	'Send':'',
	'pause':'',
	'muted':'',
	'settings':'',
	'full page(P)':'(P)',
	'full screen(F)':'(F)',
	'volume($0)([shift]+)':'($0)([shift]+)',
	'hex color':'Hex',
	'danmaku input(Enter)':'()',
	'Input danmaku here':'',
	'Failed to change to fullscreen mode':'',
}



//automatically select a language

if(!navigator.languages){
	navigator.languages=[navigator.language];
}

for(let lang of [...navigator.languages]){
	if(i18n.langs[lang]){
		i18n.lang=lang;
		break;
	}
	let code=lang.match(/^\w+/)[0];
	for(let cod in i18n.langs){
		if(cod.startsWith(code)){
			i18n.lang=cod;
			break;
		}
	}
	if(i18n.lang)break;
}
console.debug('Language:'+i18n.lang)

export {i18n};